html,body{
  margin:0;
  padding: 0;
}


.block > .backImg{
  width: 100%;
  height: 100vh;
  opacity: 0.6;
  z-index: -1;
  transform: scale(1);
  transition: opacity 1s, transform 1s;
  position: relative;
}

.block:hover > .backImg{
  transform: scale(1.02);
  opacity: 1;
}

.block {
  width: 100%;
  height: 100vh;
position:absolute;
}


.block::before{
  content:"여행 도우미 (NAGA)";
  color:#f2f2f2;
  position:absolute;
  top:20%;
  left:50%;
  transform:translateX(-50%) translateY(-50%);
  font-size: 3.5rem;
  z-index:1;
  padding:10px;
  font-weight:bold;
  opacity:0;
  transition:opacity 1s;
}

.block::after{
  content:"바로가기";
  color:#f2f2f2;
  position:absolute;
  top:80%;
  left:50%;
  transform:translateX(-50%) translateY(-50%);
  font-size: 3rem;
  padding:10px;
  font-weight:bold;
  opacity:0;
  transition:opacity 1s;
}


div:hover::before{
  opacity:1;
}

div:hover::after{
  opacity: 1;
}